.form {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.label {
  width: 400px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 5px auto;
  align-items: center;
  font-size: large;
  text-align: left;
  padding: 5px 1rem 5px 1rem;
  color: teal;
}


.input {
  margin: 5px;
  width: 250px
}

